import { Link } from 'react-router-dom';
// import { useState, useEffect } from 'react';
function Hot({ product, onAddToCart, categories }) {
    const Books = [
        {
            id: 2,
            img: "/imgs/books/1984.webp",
            alt: " 1984",
            width: "245",
            height: "300",
            title: " 1984",
            pref: "     ( 1984 )   .         .                 .",
        },
        {
            id: 3,
            img: "/imgs/books/-.webp",
            alt: "  ",
            width: "245",
            height: "300",
            title: "  ",
            pref: "             ..  ..      ..       ..     ..   ..  .",
        },
        {
            id: 4,
            img: "/imgs/books/-.webp",
            alt: "  ",
            width: "245",
            height: "300",
            title: "  ",
            pref: "    ..           ..        ..            ...     ",
        },
        {
            id: 5,
            img: "/imgs/books/.webp",
            alt: " ",
            width: "245",
            height: "300",
            title: " ",
            pref: "        '  :   '           2017                       ' '  ' '  ' '.",
        },
        {
            id: 6,
            img: "/imgs/books/-.webp",
            alt: "  ",
            width: "245",
            height: "300",
            title: "  ",
            pref: "                                                               ",
        },
        {
            id: 7,
            img: "/imgs/books/.webp",
            alt: " ",
            width: "245",
            height: "300",
            title: " ",
            pref: "     .      ..    .                 .                  ..             .. ..",
        },
        {
            id: 8,
            img: "/imgs/books/-.webp",
            alt: "  ",
            width: "245",
            height: "300",
            title: "  ",
            pref: "                     '  '                                  ",
        },
    ]
    return (
        <section className="hot">
            <div className="sec-title">
                <h1 className="shap"> </h1>
            </div>
            <div className="container">
                <div className="row justify-content-center">
                    {Books.map(book => (
                        <div className="col-lg-3 col-md-4 col-sm-6 col-xs-12" key={book.id}>
                            <div className="book-item">
                                <div className="book-img">
                                    <img src={book.img} width={book.width} height={book.height} alt={book.alt} />
                                    <div className="book-cart">
                                        <a id='cartbtn' aria-label="btn" href='/'>
                                            <i className="fa fa-shopping-cart" aria-hidden="true"></i>
                                        </a>
                                    </div>
                                </div>
                                <div className="book-info">
                                    <a href="http://" target="_blank" aria-label="title" rel="noopener noreferrer">
                                        <h5>{book.title}</h5>
                                    </a>
                                    <p>{book.pref}</p>
                                </div>
                            </div>
                        </div>
                    ))}
                    <div className="col-12">
                        <div className="more-btn">
                            <Link to="/arabic" id="btn" className="btn btn-primary"></Link>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
}
export default Hot;